@import "../../styles/vars";


.firstScreen {
    margin-top: 297px;   
    &__inner{
        display: flex;
        flex-direction: column;
    }
    &__title {
        font-size: 60px;
        line-height: 77px;
        text-align: left; 
        color: #000000;   
    }
    &__subTitle {
        margin-top: 15px;
        max-width: 462px;
        font-size: 28px;
        line-height: 28px
    }
    &__btn {
        margin-top: 78px;
            a {
                display: block;
            }
    } 
}

#dark .firstScreen__title {
    color: $textColor_dark;
}
@media(min-width: $ultra) {
    .firstScreen {
        margin-top: 477px;
        &__title {
            font-size: 95px;
            line-height: 122px;
        }
        &__subTitle {
            margin-top: 50px;
            max-width: 790px;
            font-size: 55px;
            line-height: 55px;
        }
        &__btn {
            margin-top: 100px;
        }  
    }
}

@media (max-width: $tablet)  {
    .firstScreen {
        margin-top: 200px;
        &__title {
            font-size: 48px;
            line-height: 61px;
        }
        &__subTitle {
            margin-top: 20px;
            max-width: 345px;
            font-size: 24px;
            line-height: 24px;
        }
        &__btn {
        margin-top: 73px;
        }
    }
}
@media(max-width: 774px) {
    .firstScreen {
        &__title {
            max-width: 380px;
        }
    }
}
@media (max-width: 498px) {
    .firstScreen {
        &__title {
            font-size: 45px;
        }
    }
}
@media(max-width: $mobile) {
    .firstScreen {
        margin-top: 106px;
        &__title {
            font-size: 20px;
            line-height: 26px;
            max-width: 380px;
        }
        &__subTitle {
            max-width: 280px;
            margin-top: 18px;
            font-size: 15px; 
            line-height: 15px;   
        }
        &__btn {
            margin-top: 24px; 
        }
    }   
}
@media screen and (orientation:landscape) and (max-width: $horizonMobile) {
    .firstScreen {
        margin-top: 100px; 
    }
}
